import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography
} from "@mui/material";

export default function BasicTextFields() {
  return (
    <div style={{ display: "flex" }}>
      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="450"
          image="shinderera.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            4位
          </Typography>
          <Typography gutterBottom variant="h7" component="div">
            シンデレラボーイ
          </Typography>
          <Typography variant="body2" color="text.secondary">
            蒼は職場間で渡すためのチョコレートです。チョコレートには、出世、商売繁盛などの意味が込められています。
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Share</Button>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>

      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="450"
          image="marigorudo.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            5位
          </Typography>
          <Typography gutterBottom variant="h7" component="div">
            マリーゴールド　
          </Typography>
          <Typography variant="body2" color="text.secondary">
            紅はパートナー間で渡すためのチョコレートです。チョコレートには、未来、豊か、夫婦円満などの意味が込められています。
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Share</Button>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
    </div>
  );
}
